"use client"
import { useState, useEffect, useMemo, useRef } from "react"
import Loading from "@/components/Loading"
import toast from "react-hot-toast"
import {
  Plus,
  Search,
  Filter,
  Edit3,
  Eye,
  Check,
  X,
  Clock,
  Users,
  Building,
  Mail,
  Phone,
  BadgeCheck,
  MapPin,
  Calendar,
  ExternalLink,
  Download,
  MessageSquare,
  Trash2,
  MoreHorizontal,
  Star,
  Building2,
  Globe,
  FileText,
  UserCheck,
  AlertTriangle,
  ChevronDown,
  Briefcase,
  User
} from "lucide-react"
import { API_ROUTES } from '@/lib/apiRoutes'
import { useAuthStore } from '@/stores/authStore'

export default function AdminEmployers() {
  const authStore = useAuthStore()
  
  // State management
  const [employers, setEmployers] = useState([])
  const [jobs, setJobs] = useState([])
  const [applications, setApplications] = useState([])
  const [scheduledInterviews, setScheduledInterviews] = useState([])
  const [loading, setLoading] = useState(true)
  const [searchTerm, setSearchTerm] = useState("")
  const [filterStatus, setFilterStatus] = useState("all")
  const [sortBy, setSortBy] = useState("newest")
  const [currentPage, setCurrentPage] = useState(1)
  const [itemsPerPage] = useState(12)
  
  // Modal states
  const [showCreateModal, setShowCreateModal] = useState(false)
  const [showEditModal, setShowEditModal] = useState(false)
  const [showDetailModal, setShowDetailModal] = useState(false)
  const [showApprovalModal, setShowApprovalModal] = useState(false)
  const [showJobsModal, setShowJobsModal] = useState(false)
  const [showApplicationsModal, setShowApplicationsModal] = useState(false)
  const [showInterviewsModal, setShowInterviewsModal] = useState(false)
  const [showDeleteModal, setShowDeleteModal] = useState(false)
  
  // Selected items
  const [selectedEmployer, setSelectedEmployer] = useState(null)
  const [selectedJobs, setSelectedJobs] = useState([])
  const [selectedApplications, setSelectedApplications] = useState([])
  const [selectedInterviews, setSelectedInterviews] = useState([])
  
  // Tab management
  const [activeTab, setActiveTab] = useState('overview')
  
  // Form states
  const [actionLoading, setActionLoading] = useState(false)
  const [deleteLoading, setDeleteLoading] = useState(false)

  // API Functions
  const fetchEmployers = async () => {
    console.log('fetchEmployers called')
    setLoading(true)
    try {
      const url = `${API_ROUTES.BASE_URL}job/employers`
      console.log('Fetching from URL:', url)
      console.log('Auth token:', authStore.getToken)
      
      const response = await fetch(url, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${authStore.getToken}`,
          'Content-Type': 'application/json'
        }
      })

      console.log('Response status:', response.status)
      console.log('Response ok:', response.ok)

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()
      console.log('Response data:', data)
      
      if (data.success) {
        setEmployers(data.data || [])
      } else {
        toast.error(data.message || "Failed to fetch employers")
      }
    } catch (error) {
      console.error('Fetch employers error:', error)
      toast.error("Failed to fetch employers")
    } finally {
      setLoading(false)
    }
  }

  const fetchEmployerJobs = async (employerId) => {
    try {
      const response = await fetch(`${API_ROUTES.BASE_URL}job/employer/${employerId}`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${authStore.getToken}`,
          'Content-Type': 'application/json'
        }
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()
      return data.success ? data.data || [] : []
    } catch (error) {
      console.error('Fetch employer jobs error:', error)
      return []
    }
  }

  const fetchEmployerApplications = async (employerId) => {
    try {
      const response = await fetch(`${API_ROUTES.BASE_URL}applications/employer/${employerId}`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${authStore.getToken}`,
          'Content-Type': 'application/json'
        }
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()
      return data.success ? data.data || [] : []
    } catch (error) {
      console.error('Fetch employer applications error:', error)
      return []
    }
  }

  const fetchEmployerInterviews = async (employerId) => {
    try {
      const response = await fetch(`${API_ROUTES.BASE_URL}interviews/employer/${employerId}`, {
        method: 'GET',
        headers: {
          'Authorization': `Bearer ${authStore.getToken}`,
          'Content-Type': 'application/json'
        }
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()
      return data.success ? data.data || [] : []
    } catch (error) {
      console.error('Fetch employer interviews error:', error)
      return []
    }
  }

  const toggleEmployerActive = async (employerId) => {
    setActionLoading(true)
    try {
      const response = await fetch(`${API_ROUTES.BASE_URL}job/employers/${employerId}/toggle-active`, {
        method: 'PATCH',
        headers: {
          'Authorization': `Bearer ${authStore.getToken}`,
          'Content-Type': 'application/json'
        }
      })
      
      if (!response.ok) {
        throw new Error('Failed to update employer status')
      }
      
      await fetchEmployers()
      toast.success("Employer status updated successfully")
    } catch (error) {
      console.error('Error updating employer status:', error)
      toast.error("Failed to update employer status")
    } finally {
      setActionLoading(false)
    }
  }

  const updateEmployerStatus = async (employerId, newStatus) => {
    setActionLoading(true)
    try {
      const response = await fetch(`${API_ROUTES.BASE_URL}employers/${employerId}/status`, {
        method: 'PATCH',
        headers: {
          'Authorization': `Bearer ${authStore.getToken}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          status: newStatus
        })
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()
      
      if (data.success) {
        const updatedEmployers = employers.map(emp =>
          emp.id === employerId ? { ...emp, status: newStatus } : emp
        )
        setEmployers(updatedEmployers)
        setShowApprovalModal(false)
        toast.success(`Employer ${newStatus} successfully`)
      } else {
        toast.error(data.message || `Failed to ${newStatus} employer`)
      }
    } catch (error) {
      console.error('Update employer status error:', error)
      toast.error(`Failed to ${newStatus} employer`)
    } finally {
      setActionLoading(false)
    }
  }

  const deleteEmployer = async (employerId) => {
    setDeleteLoading(true)
    try {
      const response = await fetch(`${API_ROUTES.BASE_URL}employers/${employerId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${authStore.getToken}`,
          'Content-Type': 'application/json'
        }
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()
      
      if (data.success) {
        const updatedEmployers = employers.filter(emp => emp.id !== employerId)
        setEmployers(updatedEmployers)
        setShowDeleteModal(false)
        toast.success("Employer deleted successfully")
      } else {
        toast.error(data.message || "Failed to delete employer")
      }
    } catch (error) {
      console.error('Delete employer error:', error)
      toast.error("Failed to delete employer")
    } finally {
      setDeleteLoading(false)
    }
  }

  const createEmployer = async (formData) => {
    try {
      const response = await fetch(`${API_ROUTES.BASE_URL}employers`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${authStore.getToken}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()
      
      if (data.success) {
        await fetchEmployers() // Refresh the list
        toast.success("Employer created successfully!")
        return { success: true }
      } else {
        toast.error(data.message || "Failed to create employer")
        return { success: false }
      }
    } catch (error) {
      console.error('Create employer error:', error)
      toast.error("Failed to create employer")
      return { success: false }
    }
  }

  const updateEmployer = async (employerId, formData) => {
    try {
      const response = await fetch(`${API_ROUTES.BASE_URL}employers/${employerId}`, {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${authStore.getToken}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()
      
      if (data.success) {
        await fetchEmployers() // Refresh the list
        toast.success("Employer updated successfully!")
        return { success: true }
      } else {
        toast.error(data.message || "Failed to update employer")
        return { success: false }
      }
    } catch (error) {
      console.error('Update employer error:', error)
      toast.error("Failed to update employer")
      return { success: false }
    }
  }

  // Computed values
  const filteredEmployers = useMemo(() => {
    let filtered = employers

    if (searchTerm) {
      const query = searchTerm.toLowerCase()
      filtered = filtered.filter(emp =>
        emp.name.toLowerCase().includes(query) ||
        emp.contactName.toLowerCase().includes(query) ||
        emp.email.toLowerCase().includes(query) ||
        emp.location.toLowerCase().includes(query) ||
        emp.industry.toLowerCase().includes(query)
      )
    }

    if (filterStatus !== "all") {
      filtered = filtered.filter(emp => emp.status === filterStatus)
    }

    // Sort employers
    filtered.sort((a, b) => {
      switch (sortBy) {
        case 'newest':
          return new Date(b.createdAt) - new Date(a.createdAt)
        case 'oldest':
          return new Date(a.createdAt) - new Date(b.createdAt)
        case 'name':
          return a.name.localeCompare(b.name)
        case 'status':
          return a.status.localeCompare(b.status)
        case 'jobs':
          return (b.totalJobs || 0) - (a.totalJobs || 0)
        case 'applications':
          return (b.totalApplications || 0) - (a.totalApplications || 0)
        default:
          return 0
      }
    })

    return filtered
  }, [employers, searchTerm, filterStatus, sortBy])

  const paginatedEmployers = useMemo(() => {
    const start = (currentPage - 1) * itemsPerPage
    const end = start + itemsPerPage
    return filteredEmployers.slice(start, end)
  }, [filteredEmployers, currentPage, itemsPerPage])

  const totalPages = Math.ceil(filteredEmployers.length / itemsPerPage)

  const getStatusStats = () => ({
    total: employers.length,
    approved: employers.filter(e => e.status === 'approved').length,
    pending: employers.filter(e => e.status === 'pending').length,
    rejected: employers.filter(e => e.status === 'rejected').length,
    active: employers.filter(e => e.isActive).length,
  })

  // Modal handlers
  const openEmployerDetail = async (employer) => {
    setSelectedEmployer(employer)
    setActiveTab('overview')
    setShowDetailModal(true)
    
    // Fetch related data
    try {
      const [employerJobs, employerApplications, employerInterviews] = await Promise.all([
        fetchEmployerJobs(employer.id),
        fetchEmployerApplications(employer.id),
        fetchEmployerInterviews(employer.id)
      ])
      
      setSelectedJobs(employerJobs)
      setSelectedApplications(employerApplications)
      setSelectedInterviews(employerInterviews)
    } catch (error) {
      console.error('Error fetching employer details:', error)
    }
  }

  const openApprovalModal = (employer) => {
    setSelectedEmployer(employer)
    setShowApprovalModal(true)
  }

  const openDeleteModal = (employer) => {
    setSelectedEmployer(employer)
    setShowDeleteModal(true)
  }

  // Utility functions
  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    })
  }

  const getStatusColor = (status) => {
    switch (status) {
      case 'approved': return 'bg-green-100 text-green-800 border-green-200'
      case 'pending': return 'bg-orange-100 text-orange-800 border-orange-200'
      case 'rejected': return 'bg-red-100 text-red-800 border-red-200'
      default: return 'bg-gray-100 text-gray-800 border-gray-200'
    }
  }

  const getStatusIcon = (status) => {
    switch (status) {
      case 'approved': return <Check size={10} />
      case 'pending': return <Clock size={10} />
      case 'rejected': return <X size={10} />
      default: return null
    }
  }

  useEffect(() => {
    console.log('Component mounted, auth status:', authStore.isAuthenticated)
    console.log('Auth token:', authStore.getToken)
    
    if (!authStore.isAuthenticated) {
      console.log('User not authenticated')
      return
    }
    
    console.log('Calling fetchEmployers...')
    fetchEmployers()
  }, [authStore.isAuthenticated])

  useEffect(() => {
    if (currentPage > totalPages && totalPages > 0) {
      setCurrentPage(1)
    }
  }, [currentPage, totalPages])

  if (loading) return <Loading />

  const stats = getStatusStats()

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
        <div>
          <h1 className="text-2xl lg:text-3xl font-bold text-slate-800">
            Employer <span className="text-blue-600">Management</span>
          </h1>
          <p className="text-slate-600 mt-1">Manage and verify employers on the platform.</p>
        </div>
        <button
          onClick={() => setShowCreateModal(true)}
          className="inline-flex items-center gap-2 bg-blue-600 hover:bg-blue-700 text-white px-4 py-2.5 rounded-lg font-medium transition-colors"
        >
          <Plus size={18} />
          <span>Add Employer</span>
        </button>
      </div>

      {/* Enhanced Stats Cards */}
      <div className="grid grid-cols-2 lg:grid-cols-5 gap-4">
        <div className="bg-white border border-slate-200 rounded-lg p-4">
          <div className="flex items-center gap-3">
            <div className="p-2 bg-blue-100 rounded-lg">
              <Users size={20} className="text-blue-600" />
            </div>
            <div>
              <p className="text-sm text-slate-600">Total</p>
              <p className="text-xl font-bold text-slate-800">{stats.total}</p>
            </div>
          </div>
        </div>
        <div className="bg-white border border-slate-200 rounded-lg p-4">
          <div className="flex items-center gap-3">
            <div className="p-2 bg-green-100 rounded-lg">
              <Check size={20} className="text-green-600" />
            </div>
            <div>
              <p className="text-sm text-slate-600">Approved</p>
              <p className="text-xl font-bold text-slate-800">{stats.approved}</p>
            </div>
          </div>
        </div>
        <div className="bg-white border border-slate-200 rounded-lg p-4">
          <div className="flex items-center gap-3">
            <div className="p-2 bg-orange-100 rounded-lg">
              <Clock size={20} className="text-orange-600" />
            </div>
            <div>
              <p className="text-sm text-slate-600">Pending</p>
              <p className="text-xl font-bold text-slate-800">{stats.pending}</p>
            </div>
          </div>
        </div>
        <div className="bg-white border border-slate-200 rounded-lg p-4">
          <div className="flex items-center gap-3">
            <div className="p-2 bg-red-100 rounded-lg">
              <X size={20} className="text-red-600" />
            </div>
            <div>
              <p className="text-sm text-slate-600">Rejected</p>
              <p className="text-xl font-bold text-slate-800">{stats.rejected}</p>
            </div>
          </div>
        </div>
        <div className="bg-white border border-slate-200 rounded-lg p-4">
          <div className="flex items-center gap-3">
            <div className="p-2 bg-blue-100 rounded-lg">
              <BadgeCheck size={20} className="text-blue-600" />
            </div>
            <div>
              <p className="text-sm text-slate-600">Active</p>
              <p className="text-xl font-bold text-slate-800">{stats.active}</p>
            </div>
          </div>
        </div>
      </div>

      {/* Enhanced Filters and Search */}
      <div className="bg-white border border-slate-200 rounded-lg p-6">
        <div className="flex flex-col lg:flex-row gap-4">
          {/* Search */}
          <div className="flex-1 relative">
            <Search size={18} className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400" />
            <input
              type="text"
              placeholder="Search employers by name, contact, email, location, or industry..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full pl-10 pr-4 py-2.5 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>
          
          {/* Filters */}
          <div className="flex items-center gap-4">
            <div className="flex items-center gap-2">
              <Filter size={18} className="text-slate-400" />
              <select
                value={filterStatus}
                onChange={(e) => setFilterStatus(e.target.value)}
                className="px-3 py-2.5 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              >
                <option value="all">All Status</option>
                <option value="approved">Approved</option>
                <option value="pending">Pending</option>
                <option value="rejected">Rejected</option>
              </select>
            </div>
            
            <div className="flex items-center gap-2">
              <span className="text-sm text-slate-600">Sort by:</span>
              <select
                value={sortBy}
                onChange={(e) => setSortBy(e.target.value)}
                className="px-3 py-2.5 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              >
                <option value="newest">Newest First</option>
                <option value="oldest">Oldest First</option>
                <option value="name">Company Name</option>
                <option value="status">Status</option>
                <option value="jobs">Total Jobs</option>
                <option value="applications">Applications</option>
              </select>
            </div>
          </div>
        </div>
      </div>

      {/* Employers Grid */}
      {filteredEmployers.length > 0 ? (
        <>
          <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-6">
            {paginatedEmployers.map((employer) => (
              <div 
                key={employer.id} 
                className="bg-white border border-slate-200 rounded-lg shadow-sm hover:shadow-lg transition-all duration-200 overflow-hidden"
              >
                <div className="p-6">
                  {/* Header */}
                  <div className="flex items-start justify-between mb-4">
                    <div className="flex items-start gap-3 flex-1 min-w-0">
                      <img 
                        src={employer.logo || "/logo.png"} 
                        alt={employer.name} 
                        className="w-12 h-12 object-contain rounded-lg border border-slate-100 flex-shrink-0" 
                      />
                      <div className="flex-1 min-w-0">
                        <h3 
                          className="font-bold text-slate-800 truncate cursor-pointer hover:text-blue-600 transition-colors"
                          onClick={() => openEmployerDetail(employer)}
                        >
                          {employer.name}
                        </h3>
                        <p className="text-sm text-blue-600 truncate">{employer.contactName}</p>
                        <p className="text-xs text-slate-500 truncate">{employer.industry}</p>
                      </div>
                    </div>
                    
                    {/* Status Badge */}
                    <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-semibold border ${getStatusColor(employer.status)}`}>
                      {getStatusIcon(employer.status)}
                      <span className="ml-1">{employer.status.charAt(0).toUpperCase() + employer.status.slice(1)}</span>
                    </span>
                  </div>

                  {/* Company Details */}
                  <div className="space-y-2 mb-4">
                    <div className="flex items-center gap-2 text-xs text-slate-600">
                      <MapPin size={12} className="text-blue-600 flex-shrink-0" />
                      <span className="truncate">{employer.location}</span>
                    </div>
                    <div className="flex items-center gap-2 text-xs text-slate-600">
                      <Users size={12} className="text-blue-600 flex-shrink-0" />
                      <span>{employer.companySize} employees</span>
                    </div>
                    <div className="flex items-center gap-2 text-xs text-slate-600">
                      <Mail size={12} className="text-blue-600 flex-shrink-0" />
                      <span className="truncate">{employer.email}</span>
                    </div>
                    <div className="flex items-center gap-2 text-xs text-slate-600">
                      <Phone size={12} className="text-blue-600 flex-shrink-0" />
                      <span>{employer.phone}</span>
                    </div>
                  </div>

                  {/* Statistics */}
                  <div className="grid grid-cols-2 gap-4 mb-4 p-3 bg-slate-50 rounded-lg">
                    <div className="text-center">
                      <div className="text-lg font-bold text-slate-800">{employer.totalJobs || 0}</div>
                      <div className="text-xs text-slate-600">Total Jobs</div>
                    </div>
                    <div className="text-center">
                      <div className="text-lg font-bold text-slate-800">{employer.totalApplications || 0}</div>
                      <div className="text-xs text-slate-600">Applications</div>
                    </div>
                  </div>

                  {/* Rating */}
                  <div className="flex items-center justify-between mb-4">
                    <div className="flex items-center gap-1">
                      <Star size={12} className="text-yellow-500 fill-current" />
                      <span className="text-sm font-medium text-slate-700">{employer.averageRating || 0}</span>
                      <span className="text-xs text-slate-500">rating</span>
                    </div>
                    <div className="text-xs text-slate-500">
                      Last login: {employer.lastLogin ? formatDate(employer.lastLogin) : 'Never'}
                    </div>
                  </div>

                  {/* Active Toggle */}
                  <div className="flex items-center justify-between mb-4">
                    <span className="text-sm font-medium text-slate-700">Active Status</span>
                    <label className="relative inline-flex items-center cursor-pointer">
                      <input
                        type="checkbox"
                        className="sr-only peer"
                        checked={employer.isActive}
                        onChange={() => toggleEmployerActive(employer.id)}
                        disabled={actionLoading}
                      />
                      <div className="w-9 h-5 bg-slate-300 rounded-full peer peer-checked:bg-blue-600 transition-colors duration-200"></div>
                      <span className="absolute left-0.5 top-0.5 w-4 h-4 bg-white rounded-full transition-transform duration-200 ease-in-out peer-checked:translate-x-4"></span>
                    </label>
                  </div>

                  {/* Actions */}
                  <div className="flex gap-2">
                    <button
                      onClick={() => openEmployerDetail(employer)}
                      className="flex-1 flex items-center justify-center gap-1 px-3 py-2 bg-blue-100 hover:bg-blue-200 text-blue-700 rounded-lg text-xs font-medium transition-colors"
                    >
                      <Eye size={12} />
                      View Details
                    </button>
                    
                    {employer.status === 'pending' && (
                      <button
                        onClick={() => openApprovalModal(employer)}
                        className="flex-1 flex items-center justify-center gap-1 px-3 py-2 bg-green-100 hover:bg-green-200 text-green-700 rounded-lg text-xs font-medium transition-colors"
                      >
                        <Check size={12} />
                        Review
                      </button>
                    )}
                    
                    <button
                      onClick={() => { setSelectedEmployer(employer); setShowEditModal(true) }}
                      className="flex items-center justify-center gap-1 px-3 py-2 bg-slate-100 hover:bg-slate-200 text-slate-700 rounded-lg text-xs font-medium transition-colors"
                    >
                      <Edit3 size={12} />
                    </button>
                    
                    <button
                      onClick={() => openDeleteModal(employer)}
                      className="flex items-center justify-center gap-1 px-3 py-2 bg-red-100 hover:bg-red-200 text-red-700 rounded-lg text-xs font-medium transition-colors"
                    >
                      <Trash2 size={12} />
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Pagination */}
          {totalPages > 1 && (
            <div className="flex items-center justify-between">
              <div className="text-sm text-slate-600">
                Showing {((currentPage - 1) * itemsPerPage) + 1} to {Math.min(currentPage * itemsPerPage, filteredEmployers.length)} of {filteredEmployers.length} employers
              </div>
              
              <div className="flex items-center gap-2">
                <button
                  onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
                  disabled={currentPage === 1}
                  className="flex items-center gap-2 px-3 py-2 text-sm text-slate-600 bg-white border border-slate-300 rounded-lg hover:bg-slate-50 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  Previous
                </button>
                
                <div className="flex gap-1">
                  {Array.from({ length: Math.min(5, totalPages) }, (_, i) => {
                    const page = currentPage <= 3 ? i + 1 : 
                                 currentPage >= totalPages - 2 ? totalPages - 4 + i :
                                 currentPage - 2 + i
                    return page > 0 && page <= totalPages ? (
                      <button
                        key={page}
                        onClick={() => setCurrentPage(page)}
                        className={`px-3 py-2 text-sm rounded-lg transition-colors ${
                          page === currentPage
                            ? 'bg-blue-600 text-white'
                            : 'bg-white border border-slate-300 text-slate-600 hover:bg-slate-50'
                        }`}
                      >
                        {page}
                      </button>
                    ) : null
                  })}
                </div>
                
                <button
                  onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
                  disabled={currentPage === totalPages}
                  className="flex items-center gap-2 px-3 py-2 text-sm text-slate-600 bg-white border border-slate-300 rounded-lg hover:bg-slate-50 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  Next
                </button>
              </div>
            </div>
          )}
        </>
      ) : (
        <div className="bg-white border border-slate-200 rounded-lg p-12 text-center">
          <Users size={48} className="mx-auto text-slate-400 mb-4" />
          <h3 className="text-xl font-semibold text-slate-800 mb-2">No employers found</h3>
          <p className="text-slate-600 mb-6">
            {searchTerm || filterStatus !== "all"
              ? "Try adjusting your search or filter criteria"
              : "Get started by adding your first employer"
            }
          </p>
          {(!searchTerm && filterStatus === "all") && (
            <button
              onClick={() => setShowCreateModal(true)}
              className="inline-flex items-center gap-2 bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg font-medium transition-colors"
            >
              <Plus size={16} />
              Add Employer
            </button>
          )}
        </div>
      )}

      {/* Modals */}
      {showCreateModal && (
        <CreateEmployerModal 
          onClose={() => setShowCreateModal(false)} 
          onSubmit={createEmployer}
        />
      )}
      {showEditModal && (
        <EditEmployerModal 
          employer={selectedEmployer} 
          onClose={() => setShowEditModal(false)} 
          onSubmit={updateEmployer}
        />
      )}
      {showDetailModal && (
        <EmployerDetailModal 
          employer={selectedEmployer}
          jobs={selectedJobs}
          applications={selectedApplications}
          interviews={selectedInterviews}
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          onClose={() => setShowDetailModal(false)}
        />
      )}
      {showApprovalModal && (
        <ApprovalModal
          employer={selectedEmployer}
          onApprove={(id) => updateEmployerStatus(id, 'approved')}
          onReject={(id) => updateEmployerStatus(id, 'rejected')}
          onClose={() => setShowApprovalModal(false)}
          loading={actionLoading}
        />
      )}
      {showDeleteModal && (
        <DeleteConfirmModal
          employer={selectedEmployer}
          onConfirm={() => deleteEmployer(selectedEmployer.id)}
          onClose={() => setShowDeleteModal(false)}
          loading={deleteLoading}
        />
      )}
    </div>
  )
}

// Create Employer Modal
const CreateEmployerModal = ({ onClose, onSubmit }) => {
  const [formData, setFormData] = useState({
    name: '',
    contactName: '',
    email: '',
    phone: '',
    location: '',
    description: '',
    website: '',
    industry: '',
    companySize: '',
    logo: ''
  })
  const [loading, setLoading] = useState(false)

  const handleSubmit = async (e) => {
    e.preventDefault()
    setLoading(true)
    try {
      const result = await onSubmit(formData)
      if (result.success) {
        onClose()
      }
    } catch (error) {
      console.error('Create employer error:', error)
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg max-w-3xl w-full max-h-[90vh] overflow-y-auto">
        <div className="sticky top-0 bg-white border-b border-slate-200 px-6 py-4">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-bold text-slate-800">Add New Employer</h2>
            <button onClick={onClose} className="p-2 hover:bg-slate-100 rounded-lg">
              <X size={20} className="text-slate-600" />
            </button>
          </div>
        </div>

        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Company Name *</label>
              <input
                type="text"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Contact Person *</label>
              <input
                type="text"
                value={formData.contactName}
                onChange={(e) => setFormData({ ...formData, contactName: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Email *</label>
              <input
                type="email"
                value={formData.email}
                onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Phone *</label>
              <input
                type="tel"
                value={formData.phone}
                onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Location *</label>
              <input
                type="text"
                value={formData.location}
                onChange={(e) => setFormData({ ...formData, location: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Website</label>
              <input
                type="url"
                value={formData.website}
                onChange={(e) => setFormData({ ...formData, website: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                placeholder="https://"
              />
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Industry *</label>
              <select
                value={formData.industry}
                onChange={(e) => setFormData({ ...formData, industry: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              >
                <option value="">Select Industry</option>
                <option value="Technology">Technology</option>
                <option value="Finance">Finance</option>
                <option value="Healthcare">Healthcare</option>
                <option value="Education">Education</option>
                <option value="Manufacturing">Manufacturing</option>
                <option value="Retail">Retail</option>
                <option value="Agriculture">Agriculture</option>
                <option value="Other">Other</option>
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Company Size</label>
              <select
                value={formData.companySize}
                onChange={(e) => setFormData({ ...formData, companySize: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
              >
                <option value="">Select Size</option>
                <option value="1-10">1-10 employees</option>
                <option value="11-50">11-50 employees</option>
                <option value="51-200">51-200 employees</option>
                <option value="201-500">201-500 employees</option>
                <option value="501-1000">501-1000 employees</option>
                <option value="1000+">1000+ employees</option>
              </select>
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-slate-700 mb-1">Company Description *</label>
            <textarea
              value={formData.description}
              onChange={(e) => setFormData({ ...formData, description: e.target.value })}
              rows={4}
              className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
              required
            />
          </div>

          <div className="flex justify-end gap-3 pt-4">
            <button 
              type="button" 
              onClick={onClose} 
              className="px-4 py-2 text-slate-600 hover:bg-slate-100 rounded-lg transition-colors"
              disabled={loading}
            >
              Cancel
            </button>
            <button 
              type="submit" 
              className="px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg transition-colors disabled:opacity-50"
              disabled={loading}
            >
              {loading ? 'Creating...' : 'Add Employer'}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}

// Enhanced Employer Detail Modal
const EmployerDetailModal = ({ employer, jobs, applications, interviews, activeTab, setActiveTab, onClose }) => {
  if (!employer) return null

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    })
  }

  const getStatusColor = (status) => {
    switch (status) {
      case 'approved': case 'active': case 'hired': return 'text-green-600 bg-green-100'
      case 'pending': case 'interview_scheduled': return 'text-orange-600 bg-orange-100' 
      case 'rejected': case 'declined': return 'text-red-600 bg-red-100'
      case 'under_review': return 'text-blue-600 bg-blue-100'
      default: return 'text-gray-600 bg-gray-100'
    }
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg max-w-6xl w-full max-h-[90vh] overflow-hidden">
        {/* Header */}
        <div className="sticky top-0 bg-white border-b border-slate-200 px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <img 
                src={employer.logo || "/logo.png"} 
                alt={employer.name} 
                className="w-12 h-12 object-contain rounded-lg border" 
              />
              <div>
                <h2 className="text-xl font-bold text-slate-800">{employer.name}</h2>
                <p className="text-slate-600">{employer.contactName} • {employer.industry}</p>
              </div>
            </div>
            <button onClick={onClose} className="p-2 hover:bg-slate-100 rounded-lg">
              <X size={20} className="text-slate-600" />
            </button>
          </div>
        </div>

        {/* Tab Navigation */}
        <div className="border-b border-slate-200">
          <nav className="flex space-x-8 px-6">
            {[
              { id: 'overview', label: 'Overview', icon: Building2 },
              { id: 'jobs', label: `Jobs (${jobs.length})`, icon: Briefcase },
              { id: 'applications', label: `Applications (${applications.length})`, icon: Users },
              { id: 'interviews', label: `Interviews (${interviews.length})`, icon: Calendar },
              { id: 'documents', label: 'Documents', icon: FileText }
            ].map((tab) => (
              <button
                key={tab.id}
                onClick={() => setActiveTab(tab.id)}
                className={`py-3 px-1 border-b-2 font-medium text-sm flex items-center gap-2 ${
                  activeTab === tab.id
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-slate-500 hover:text-slate-700'
                }`}
              >
                <tab.icon size={16} />
                {tab.label}
              </button>
            ))}
          </nav>
        </div>

        {/* Tab Content */}
        <div className="p-6 overflow-y-auto max-h-[calc(90vh-140px)]">
          {/* Overview Tab */}
          {activeTab === 'overview' && (
            <div className="space-y-6">
              {/* Company Information */}
              <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                <div className="lg:col-span-2">
                  <h3 className="text-lg font-semibold mb-4">Company Information</h3>
                  <div className="bg-slate-50 rounded-lg p-4 space-y-4">
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <span className="text-sm font-medium text-slate-600">Status</span>
                        <div className={`inline-block px-2 py-1 rounded-full text-xs font-medium mt-1 ${getStatusColor(employer.status)}`}>
                          {employer.status.charAt(0).toUpperCase() + employer.status.slice(1)}
                        </div>
                      </div>
                      <div>
                        <span className="text-sm font-medium text-slate-600">Active</span>
                        <div className={`text-sm mt-1 ${employer.isActive ? 'text-green-600' : 'text-red-600'}`}>
                          {employer.isActive ? 'Yes' : 'No'}
                        </div>
                      </div>
                    </div>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="flex items-center gap-2">
                        <Mail size={16} className="text-blue-600" />
                        <span className="text-sm">{employer.email}</span>
                      </div>
                      <div className="flex items-center gap-2">
                        <Phone size={16} className="text-blue-600" />
                        <span className="text-sm">{employer.phone}</span>
                      </div>
                      <div className="flex items-center gap-2">
                        <MapPin size={16} className="text-blue-600" />
                        <span className="text-sm">{employer.location}</span>
                      </div>
                      {employer.website && (
                        <div className="flex items-center gap-2">
                          <Globe size={16} className="text-blue-600" />
                          <a 
                            href={employer.website} 
                            target="_blank" 
                            rel="noopener noreferrer"
                            className="text-sm text-blue-600 hover:underline flex items-center gap-1"
                          >
                            Website <ExternalLink size={12} />
                          </a>
                        </div>
                      )}
                      <div className="flex items-center gap-2">
                        <Users size={16} className="text-blue-600" />
                        <span className="text-sm">{employer.companySize} employees</span>
                      </div>
                      <div className="flex items-center gap-2">
                        <Calendar size={16} className="text-blue-600" />
                        <span className="text-sm">Joined {formatDate(employer.createdAt)}</span>
                      </div>
                    </div>
                    <div>
                      <span className="text-sm font-medium text-slate-600">Description</span>
                      <p className="text-sm text-slate-700 mt-1">{employer.description}</p>
                    </div>
                  </div>
                </div>

                {/* Statistics */}
                <div>
                  <h3 className="text-lg font-semibold mb-4">Statistics</h3>
                  <div className="space-y-4">
                    <div className="bg-blue-50 rounded-lg p-4">
                      <div className="text-2xl font-bold text-blue-600">{employer.totalJobs || 0}</div>
                      <div className="text-sm text-blue-700">Total Jobs Posted</div>
                      <div className="text-xs text-blue-600 mt-1">{employer.activeJobs || 0} currently active</div>
                    </div>
                    <div className="bg-green-50 rounded-lg p-4">
                      <div className="text-2xl font-bold text-green-600">{employer.totalApplications || 0}</div>
                      <div className="text-sm text-green-700">Total Applications</div>
                    </div>
                    <div className="bg-purple-50 rounded-lg p-4">
                      <div className="text-2xl font-bold text-purple-600">{employer.totalInterviews || 0}</div>
                      <div className="text-sm text-purple-700">Interviews Conducted</div>
                    </div>
                    <div className="bg-orange-50 rounded-lg p-4">
                      <div className="flex items-center gap-2">
                        <Star size={16} className="text-orange-500 fill-current" />
                        <span className="text-2xl font-bold text-orange-600">{employer.averageRating || 0}</span>
                      </div>
                      <div className="text-sm text-orange-700">Average Rating</div>
                    </div>
                    <div className="text-xs text-slate-500">
                      Last login: {employer.lastLogin ? formatDate(employer.lastLogin) : 'Never'}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Jobs Tab */}
          {activeTab === 'jobs' && (
            <div className="space-y-4">
              <h3 className="text-lg font-semibold">Job Postings</h3>
              {jobs.length > 0 ? (
                <div className="grid gap-4">
                  {jobs.map((job) => (
                    <div key={job.id} className="border border-slate-200 rounded-lg p-4">
                      <div className="flex justify-between items-start">
                        <div>
                          <h4 className="font-medium text-slate-800">{job.title}</h4>
                          <p className="text-sm text-slate-600">{job.location} • {job.salary}</p>
                          <div className="flex items-center gap-4 mt-2">
                            <span className="text-xs text-slate-500">Posted {formatDate(job.createdAt)}</span>
                            <span className="text-xs text-slate-500">{job.applications || 0} applications</span>
                          </div>
                        </div>
                        <div className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(job.status)}`}>
                          {job.status}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="text-center py-8 text-slate-500">
                  <Briefcase size={48} className="mx-auto mb-2 text-slate-300" />
                  <p>No job postings found</p>
                </div>
              )}
            </div>
          )}

          {/* Applications Tab */}
          {activeTab === 'applications' && (
            <div className="space-y-4">
              <h3 className="text-lg font-semibold">Job Applications</h3>
              {applications.length > 0 ? (
                <div className="grid gap-4">
                  {applications.map((application) => (
                    <div key={application.id} className="border border-slate-200 rounded-lg p-4">
                      <div className="flex justify-between items-start">
                        <div>
                          <h4 className="font-medium text-slate-800">{application.candidateName}</h4>
                          <p className="text-sm text-slate-600">{application.candidateEmail}</p>
                          <div className="flex items-center gap-4 mt-2">
                            <span className="text-xs text-slate-500">Applied {formatDate(application.appliedAt)}</span>
                            {application.resume && (
                              <button className="text-xs text-blue-600 hover:underline flex items-center gap-1">
                                <Download size={12} />
                                Resume
                              </button>
                            )}
                          </div>
                        </div>
                        <div className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(application.status)}`}>
                          {application.status.replace('_', ' ')}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="text-center py-8 text-slate-500">
                  <Users size={48} className="mx-auto mb-2 text-slate-300" />
                  <p>No applications found</p>
                </div>
              )}
            </div>
          )}

          {/* Interviews Tab */}
          {activeTab === 'interviews' && (
            <div className="space-y-4">
              <h3 className="text-lg font-semibold">Scheduled Interviews</h3>
              {interviews.length > 0 ? (
                <div className="grid gap-4">
                  {interviews.map((interview) => (
                    <div key={interview.id} className="border border-slate-200 rounded-lg p-4">
                      <div className="flex justify-between items-start">
                        <div>
                          <h4 className="font-medium text-slate-800">{interview.candidateName}</h4>
                          <p className="text-sm text-slate-600">{interview.jobTitle}</p>
                          <div className="flex items-center gap-4 mt-2">
                            <span className="text-xs text-slate-500">
                              {formatDate(interview.date)} at {interview.time}
                            </span>
                            <span className="text-xs text-slate-500">{interview.duration} minutes</span>
                            <span className="text-xs text-slate-500">{interview.type} interview</span>
                          </div>
                          {interview.notes && (
                            <p className="text-xs text-slate-600 mt-1">{interview.notes}</p>
                          )}
                        </div>
                        <div className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(interview.status)}`}>
                          {interview.status}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="text-center py-8 text-slate-500">
                  <Calendar size={48} className="mx-auto mb-2 text-slate-300" />
                  <p>No interviews scheduled</p>
                </div>
              )}
            </div>
          )}

          {/* Documents Tab */}
          {activeTab === 'documents' && (
            <div className="space-y-4">
              <h3 className="text-lg font-semibold">Company Documents</h3>
              {employer.documents && employer.documents.length > 0 ? (
                <div className="grid gap-3">
                  {employer.documents.map((document, index) => (
                    <div key={index} className="flex items-center justify-between p-3 border border-slate-200 rounded-lg">
                      <div className="flex items-center gap-3">
                        <FileText size={16} className="text-blue-600" />
                        <span className="text-sm font-medium text-slate-700">{document}</span>
                      </div>
                      <button className="text-blue-600 hover:text-blue-800 flex items-center gap-1">
                        <Download size={14} />
                        <span className="text-sm">Download</span>
                      </button>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="text-center py-8 text-slate-500">
                  <FileText size={48} className="mx-auto mb-2 text-slate-300" />
                  <p>No documents uploaded</p>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  )
}

// Approval Modal
const ApprovalModal = ({ employer, onApprove, onReject, onClose, loading }) => {
  if (!employer) return null

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        <div className="sticky top-0 bg-white border-b border-slate-200 px-6 py-4">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-bold text-slate-800">Review Employer Application</h2>
            <button onClick={onClose} className="p-2 hover:bg-slate-100 rounded-lg">
              <X size={20} className="text-slate-600" />
            </button>
          </div>
        </div>

        <div className="p-6 space-y-6">
          {/* Company Overview */}
          <div className="flex items-start gap-4">
            <img 
              src={employer.logo || "/logo.png"} 
              alt={employer.name} 
              className="w-16 h-16 object-contain rounded-lg border" 
            />
            <div className="flex-1">
              <h3 className="text-lg font-semibold text-slate-900">{employer.name}</h3>
              <p className="text-slate-600">{employer.industry} • {employer.companySize} employees</p>
              <p className="text-sm text-slate-600 mt-1">{employer.location}</p>
            </div>
          </div>

          {/* Contact Information */}
          <div className="bg-slate-50 rounded-lg p-4">
            <h4 className="font-medium text-slate-900 mb-3">Contact Information</h4>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
              <div className="flex items-center gap-2">
                <User size={16} className="text-slate-400" />
                <span className="text-sm">{employer.contactName}</span>
              </div>
              <div className="flex items-center gap-2">
                <Mail size={16} className="text-slate-400" />
                <span className="text-sm">{employer.email}</span>
              </div>
              <div className="flex items-center gap-2">
                <Phone size={16} className="text-slate-400" />
                <span className="text-sm">{employer.phone}</span>
              </div>
              {employer.website && (
                <div className="flex items-center gap-2">
                  <Globe size={16} className="text-slate-400" />
                  <a 
                    href={employer.website} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className="text-sm text-blue-600 hover:underline"
                  >
                    {employer.website}
                  </a>
                </div>
              )}
            </div>
          </div>

          {/* Company Description */}
          <div>
            <h4 className="font-medium text-slate-900 mb-2">Company Description</h4>
            <p className="text-sm text-slate-700 leading-relaxed">{employer.description}</p>
          </div>

          {/* Documents */}
          {employer.documents && employer.documents.length > 0 && (
            <div>
              <h4 className="font-medium text-slate-900 mb-2">Uploaded Documents</h4>
              <div className="space-y-2">
                {employer.documents.map((document, index) => (
                  <div key={index} className="flex items-center justify-between p-2 bg-slate-50 rounded">
                    <div className="flex items-center gap-2">
                      <FileText size={14} className="text-blue-600" />
                      <span className="text-sm">{document}</span>
                    </div>
                    <button className="text-blue-600 hover:text-blue-800">
                      <Download size={14} />
                    </button>
                  </div>
                ))}
              </div>
            </div>
          )}

          {/* Application Date */}
          <div className="text-xs text-slate-500">
            Application submitted on {new Date(employer.createdAt).toLocaleDateString('en-US', {
              year: 'numeric',
              month: 'long',
              day: 'numeric'
            })}
          </div>

          {/* Warning Message */}
          <div className="bg-amber-50 border border-amber-200 rounded-lg p-4">
            <div className="flex items-start gap-2">
              <AlertTriangle size={16} className="text-amber-600 mt-0.5 flex-shrink-0" />
              <div>
                <h5 className="font-medium text-amber-800">Review Carefully</h5>
                <p className="text-sm text-amber-700 mt-1">
                  Please verify all company information and documents before approving. 
                  Approved employers will be able to post jobs and access candidate profiles.
                </p>
              </div>
            </div>
          </div>

          {/* Action Buttons */}
          <div className="flex flex-col sm:flex-row gap-3 pt-4 border-t border-slate-200">
            <button
              onClick={() => onReject(employer.id)}
              disabled={loading}
              className="flex-1 flex items-center justify-center gap-2 px-4 py-3 bg-red-100 hover:bg-red-200 text-red-700 rounded-lg font-medium transition-colors disabled:opacity-50"
            >
              <X size={18} />
              {loading ? 'Processing...' : 'Reject Application'}
            </button>
            <button
              onClick={() => onApprove(employer.id)}
              disabled={loading}
              className="flex-1 flex items-center justify-center gap-2 px-4 py-3 bg-green-600 hover:bg-green-700 text-white rounded-lg font-medium transition-colors disabled:opacity-50"
            >
              <Check size={18} />
              {loading ? 'Processing...' : 'Approve Employer'}
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

// Delete Confirmation Modal
const DeleteConfirmModal = ({ employer, onConfirm, onClose, loading }) => {
  if (!employer) return null

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg max-w-md w-full">
        <div className="p-6">
          <div className="flex items-center gap-4 mb-4">
            <div className="p-3 bg-red-100 rounded-full">
              <AlertTriangle size={24} className="text-red-600" />
            </div>
            <div>
              <h3 className="text-lg font-bold text-slate-900">Delete Employer</h3>
              <p className="text-sm text-slate-600">This action cannot be undone</p>
            </div>
          </div>

          <div className="bg-slate-50 rounded-lg p-4 mb-6">
            <div className="flex items-center gap-3">
              <img 
                src={employer.logo || "/logo.png"} 
                alt={employer.name} 
                className="w-10 h-10 object-contain rounded border" 
              />
              <div>
                <div className="font-medium text-slate-900">{employer.name}</div>
                <div className="text-sm text-slate-600">{employer.contactName}</div>
              </div>
            </div>
          </div>

          <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
            <h4 className="font-medium text-red-800 mb-2">Warning</h4>
            <ul className="text-sm text-red-700 space-y-1">
              <li>• All job postings will be removed</li>
              <li>• Application data will be permanently deleted</li>
              <li>• Interview schedules will be cancelled</li>
              <li>• This action cannot be reversed</li>
            </ul>
          </div>

          <div className="flex gap-3">
            <button
              onClick={onClose}
              disabled={loading}
              className="flex-1 px-4 py-2 text-slate-600 border border-slate-300 rounded-lg hover:bg-slate-50 transition-colors disabled:opacity-50"
            >
              Cancel
            </button>
            <button
              onClick={onConfirm}
              disabled={loading}
              className="flex-1 px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors disabled:opacity-50"
            >
              {loading ? (
                <div className="flex items-center justify-center gap-2">
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                  Deleting...
                </div>
              ) : (
                'Delete Employer'
              )}
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

// Edit Employer Modal
const EditEmployerModal = ({ employer, onClose, onSubmit }) => {
  const [formData, setFormData] = useState({
    name: employer?.name || '',
    contactName: employer?.contactName || '',
    email: employer?.email || '',
    phone: employer?.phone || '',
    location: employer?.location || '',
    description: employer?.description || '',
    website: employer?.website || '',
    industry: employer?.industry || '',
    companySize: employer?.companySize || ''
  })
  const [loading, setLoading] = useState(false)

  const handleSubmit = async (e) => {
    e.preventDefault()
    setLoading(true)
    try {
      const result = await onSubmit(employer.id, formData)
      if (result.success) {
        onClose()
      }
    } catch (error) {
      console.error('Update employer error:', error)
    } finally {
      setLoading(false)
    }
  }

  if (!employer) return null

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-lg max-w-3xl w-full max-h-[90vh] overflow-y-auto">
        <div className="sticky top-0 bg-white border-b border-slate-200 px-6 py-4">
          <div className="flex items-center justify-between">
            <h2 className="text-xl font-bold text-slate-800">Edit Employer</h2>
            <button onClick={onClose} className="p-2 hover:bg-slate-100 rounded-lg">
              <X size={20} className="text-slate-600" />
            </button>
          </div>
        </div>

        <form onSubmit={handleSubmit} className="p-6 space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Company Name</label>
              <input
                type="text"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Contact Person</label>
              <input
                type="text"
                value={formData.contactName}
                onChange={(e) => setFormData({ ...formData, contactName: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Email</label>
              <input
                type="email"
                value={formData.email}
                onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Phone</label>
              <input
                type="tel"
                value={formData.phone}
                onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Location</label>
              <input
                type="text"
                value={formData.location}
                onChange={(e) => setFormData({ ...formData, location: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
                required
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Website</label>
              <input
                type="url"
                value={formData.website}
                onChange={(e) => setFormData({ ...formData, website: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
              />
            </div>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Industry</label>
              <select
                value={formData.industry}
                onChange={(e) => setFormData({ ...formData, industry: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
              >
                <option value="">Select Industry</option>
                <option value="Technology">Technology</option>
                <option value="Finance">Finance</option>
                <option value="Healthcare">Healthcare</option>
                <option value="Education">Education</option>
                <option value="Manufacturing">Manufacturing</option>
                <option value="Retail">Retail</option>
                <option value="Agriculture">Agriculture</option>
                <option value="Other">Other</option>
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-slate-700 mb-1">Company Size</label>
              <select
                value={formData.companySize}
                onChange={(e) => setFormData({ ...formData, companySize: e.target.value })}
                className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
              >
                <option value="">Select Size</option>
                <option value="1-10">1-10 employees</option>
                <option value="11-50">11-50 employees</option>
                <option value="51-200">51-200 employees</option>
                <option value="201-500">201-500 employees</option>
                <option value="501-1000">501-1000 employees</option>
                <option value="1000+">1000+ employees</option>
              </select>
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-slate-700 mb-1">Company Description</label>
            <textarea
              value={formData.description}
              onChange={(e) => setFormData({ ...formData, description: e.target.value })}
              rows={4}
              className="w-full px-3 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500"
            />
          </div>

          <div className="flex justify-end gap-3 pt-4">
            <button 
              type="button" 
              onClick={onClose} 
              className="px-4 py-2 text-slate-600 hover:bg-slate-100 rounded-lg transition-colors"
              disabled={loading}
            >
              Cancel
            </button>
            <button 
              type="submit" 
              className="px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg transition-colors disabled:opacity-50"
              disabled={loading}
            >
              {loading ? 'Updating...' : 'Update Employer'}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}



